export default {
	data() {
		return {
			title: '产品分类',
			datas: null,
			searchDatas: null,
			searchKey: '',
			showAddModal: false,
			showEditModal: false,
			addTypeName: '',
			editTypeName: ''
		}
	},
	onLoad(options) {
	},
	onShow() {
		this.datas = uni.getStorageSync('productType')
		this.datas = this.datas.filter((item) => {
			return item.name !== '所有分类'
		})
		this.searchDatas = this.datas
	},
	methods: {
		handleShowAddModal() {
			this.addTypeName = ''
			this.$nextTick(function(){
				this.showAddModal = true
			})
		},
		handleReturnItem(val) {
			let pages =  getCurrentPages()
			let prevPage = pages[pages.length - 2]
			prevPage.setData({
				selectType: val
			})
			uni.navigateBack({
				delta: 1
			})
		},
		handleShowEditModal(val) {
			this.editTypeName = val.name
			this.$nextTick(function(){
				this.showEditModal = true
			})
		},
		handleHideAddModal() {
			this.showAddModal = false
		},
		handleHideEditModal() {
			this.showEditModal = false
		},
		handleAdd() {
			if (this.addTypeName) {
				this.datas.push({ name: this.addTypeName })
				uni.setStorageSync('productType', this.datas)
				this.searchDatas = this.datas.filter((item) => {
					return item.name !== '所有分类'
				})
				this.addTypeName = ''
			}
			this.$nextTick(function(){
				this.showAddModal = false
			})
		},
		handleEdit(val) {				
			if (this.editTypeName) {
				for (let item of this.datas) {
					if (item.name == val.name) {
						item.name = this.editTypeName
					}
				}
				uni.setStorageSync('productType', this.datas)
				this.searchDatas = this.datas.filter((item) => {
					return item.name !== '所有分类'
				})
				this.editTypeName = ''
			}
			this.$nextTick(function(){
				this.showEditModal = false
			})
		},
		handleCancelSearch() {
			this.searchKey = ''
		}
	},
	watch: {
		searchKey(val) {
			if (val) {
				this.searchDatas = this.datas.filter((item) => {
					return item.name.indexOf(val) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		}
	}
}
